<template>
  <div class="greenDialogue" v-bind:style="{ width: window.width + 'px', height: '210px'}">
    <div class="greenTitle" v-show="speaker!==''">
      <div class="greenRadius">
        <p id=title>{{ speaker }}</p>
      </div>
    </div>
    <div class="greenDialogueBottom" v-bind:style="{ width: window.width + 'px', height: '180px'}">
      <div class="greenContain">
        <p>{{ message }}</p>
      </div>
      <div id="nextButton" @click="nextSentence" v-show="needShowNextButton">
        <img id="button" src= "../assets/images/commute/next.png" style="width: 29.7px; height: 25.8px;" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Dialogue',
  data () {
    return {
      window: {
        height: window.innerHeight,
        width: window.innerWidth
      },
      sentenceIndex: 0,
      characterIndex: 0,
      message: '',
      time: null,
      needShowNextButton: false
    }
  },
  props: {
    msgs: Array,
    speaker: String,
    finishConversation: Function
  },
  created () {
    this.setTime()
  },
  methods: {
    timer () {
      this.time = setInterval(() => {
        if (this.sentenceIndex >= this.msgs.length) {
          this.stopTime()
          this.needShowNextButton = true
        } else if (this.characterIndex > this.msgs[this.sentenceIndex].length) {
          this.stopTime()
          this.needShowNextButton = true
        } else {
          this.message = this.msgs[this.sentenceIndex].substring(0, this.characterIndex)
          this.characterIndex++
          // console.log(this.message)
          this.needShowNextButton = false
        }
      }, 50)
    },
    setTime () {
      this.timer()
    },
    stopTime () {
      if (this.time) {
        clearInterval(this.time)
      }
    },
    nextSentence () {
      this.characterIndex = 0
      this.sentenceIndex++
      if (this.sentenceIndex >= this.msgs.length) {
        this.finishConversation(this.msgs)
        // console.log('finish ..')
      } else {
        this.setTime()
      }
    }
  }
}
</script>

<style scoped>
  .greenDialogue {
    position: absolute;
    z-index: 1;
  }
  .greenDialogueBottom {
    background-color: #95ff00;
    position: relative;
    opacity: 0.7;
  }
  .greenTitle {
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .greenRadius {
    width: 100px;
    height: 30px;
    border-radius: 15px;
    background-color: #95ff00;
    opacity: 0.7;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  p {
    padding: 10px 20px ;
    margin: 10px;
    color: black;
    font-weight: bold;
    line-height: 1.6em;
    text-align: left;
    word-wrap: keep-all;
  }
  #title {
    font-weight: bold;
    color: black;
    padding: 0px;
  }
  #nextButton {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
</style>



