<template>
  <div class="transferWrongDiagram" v-bind:style="{ width: window.width + 'px', height: window.height + 'px'}">
    <p class="wrong">你無法及時抵達目的地</p>
  </div>
</template>

<script>
export default {
  name: 'TransferWrongDiagram',
  data () {
    return {
      window: {
        height: window.innerHeight,
        width: window.innerWidth
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import "~@/assets/scss/common.scss";
  .transferWrongDiagram {
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    .wrong {
      color: $redColor;
      font-size: 1.4em;
    }
  }
</style>



