<template>
  <div class="selection" v-bind:style="{ width: window.width + 'px', height: window.height + 'px'}">
    <div id="items">
      <div class="item" v-for="(item, index) in items" :key="index" v-bind:style="{ width: window.width + 'px', height: 80 + 'px'}" @click="answerPressed(index)"><p>{{ item }}</p></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Selection',
  data () {
    return {
      window: {
        height: window.innerHeight,
        width: window.innerWidth
      },
      sentenceIndex: -1
    }
  },
  props: {
    items: Array,
    finishSelection: Function
  },
  methods: {
    answerPressed (index) {
      console.log(index)
      this.finishSelection(index)
    }
  }
}
</script>

<style scoped>
  .selection {
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    z-index: 10;
    min-height: 100vh;
  }
  .item {
    background-image: url(../assets/images/commute/select-button-bg.png);
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0px;
  }
  p {
    color: white;
    font-weight: bold;
    margin: 0px 20px;
  }
</style>



